* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Segoe UI", Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background-color: #fcfcfc;
  color: #1f1f1f;
}

:root {
  --main-color: #678c89;
}

.header-container {
  background-color: var(--main-color);
  color: #fff;
  padding: 10px 15px;
}

.header-title {
  font-size: 25px;
  line-height: 1.447em;
  margin: 0px;
}

.container {
  max-width: 500px;
  margin: 0px auto;
  border: 1px var(--main-color) solid;
}

.todo-item {
  list-style-type: none;
  padding: 10px 15px;
  border-top: 1px var(--main-color) solid;
}

.todo-item input {
  margin-right: 15px;
}

.completed {
  font-style: italic;
  color: #c5e2d2;
  text-decoration: line-through;
}

.btn-style {
  background: #d35e0f;
  color: #fff;
  border: 1px #d35e0f solid;
  padding: 3px 7px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
  outline: none;
}

.form-container {
  display: flex;
  width: 100%;
}

.input-text {
  flex: 8;
  font-size: 14px;
  padding: 6px 15px;
  background: rgba(103, 140, 137, 0.65);
  border: none;
  color: #fff;
  outline: none;
  font-weight: 400;
  width: 80%;
}

.input-text::placeholder {
  color: #fff;
  opacity: 0.8;
}

.input-submit {
  flex: 2;
  border: none;
  background: #678c89;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 600;
  width: 20%;
  outline: none;
}

.todo-item span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 80%;
  height: 18px;
  display: inline-block;
}

.footer {
  height: 50px;
  background: var(--main-color);
  color: white;
  position: relative;
}

.vertical-center {
  margin: 0 0 0 10px;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.dot {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}

.red {
  background-color: #f56f42;
}

.blue {
  background-color: #4287f5;
}

.gray {
  background-color: #678c89;
}
